function Paid() {
  return (
    <Wrapper bgColor="#ff8b00">
      <CustomIcon className="text-primary font-size-xl" svg={IconCheck} />
      <Text>Paid</Text>
    </Wrapper>
  );
}

function Unpaid() {
  return (
    <Wrapper bgColor="#231F20">
      <CustomIcon className="text-primary font-size-xl" svg={IconUnpaid} />
      <Text>Unpaid</Text>
    </Wrapper>
  );
}

function Pending() {
  return (
    <Wrapper bgColor="#83B5D1">
      <CustomIcon className="text-primary font-size-xl" svg={IconPending} />
      <Text>Pending</Text>
    </Wrapper>
  );
}

function Perfect() {
  return <PerfectText>Perfect</PerfectText>;
}

function Error() {
  return <ErrorText>Error</ErrorText>;
}

function HoldOn() {
  return <HoldOnText>Hold On</HoldOnText>;
}

